import React from 'react';
import {Table} from 'react-bootstrap';
import {connect} from 'react-redux';
import { useSelector, useDispatch } from 'react-redux';

function Basket(props){
  // useSelector  redux state  
  let state = useSelector((state) => state )
  let dispatch = useDispatch();
  let shoesStore = state.reducer;
  let alertCheck = state.alertReducer;

  return (
    <div>
      <Table responsive>
        <tr>
          <th>#</th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        { shoesStore.map((a,i)=>{
          return (
          <tr key={i}>
            <td>{a.id}</td>
            <td>{a.name}</td>
            <td>{a.stock}</td>
            <td>
                <button onClick={()=>{ dispatch({type: 'stockCntPlus', id:a.id}) }}> + </button> &nbsp;
                <button onClick={()=>{ dispatch({type: 'stockCntMinus', id:a.id}) }}> - </button>
            </td>
          </tr>
          )
        })  }
      </Table>
      {
          alertCheck == true?(
                <div className="alert_SM">
                    <p></p>
                    <button onClick={()=>{ dispatch({type:'alertCancel'}) }}></button>
                </div>
          ):null
      }
      
    </div>
  )
}

